import Taro from '@tarojs/taro'
import { style } from '../wxs/style'
import { addUnit } from '../wxs/add-unit'
function isImage(name) {
  return name.indexOf('/') !== -1
}
function rootClass(data) {
  var classes = []
  if (data.classPrefix) {
    classes.push(data.classPrefix)
  }
  if (isImage(data.name)) {
    classes.push('van-icon--image')
  } else {
    var prefixName = data.classPrefix
      ? ''.concat(data.classPrefix, '-').concat(data.name)
      : data.name
    classes.push(prefixName)
  }
  return classes.join(' ')
}
function rootStyle(data) {
  return style([
    {
      color: data.color,
      'font-size': addUnit(data.size),
      height: addUnit(data.size),
    },
  ])
}

//复制icon
function copyIcon(name) {
  Taro.setClipboardData({
    data: "<Icon name='".concat(name, '\' size="32px" />'),
    success: function success() {
      Taro.showToast({
        title: '复制成功',
        icon: 'none',
      })
    },
  })
}
export { isImage, rootClass, rootStyle, copyIcon }
